<script>
/////////////////////////////////////////////////////

import PhotoThumbnail from '@/components/photos/PhotoThumbnail.vue';
import PhotoEventTeaser from '@/components/photos/PhotoEventTeaser.vue';
import PhotoPhotographerTeaser from '@/components/photos/PhotoPhotographerTeaser.vue';
import PhotoKeywordTeaser from '@/components/photos/PhotoKeywordTeaser.vue';
import PhotoRealmTeaser from '@/components/photos/PhotoRealmTeaser.vue';
import PhotoTagTeaser from '@/components/photos/PhotoTagTeaser.vue';

/////////////////////////////////////////////////////

import BreadcrumbMixin from '@/mixins/BreadcrumbMixin'

export default {
    data() {

        var currentRoute = this.$route.name.split('.')[1]

        // console.log('ROUTE CHECK', this.$route)

        var viewModes = [

            //List/Default View
            {
                title: 'Latest',
                value: 'latest',
                icon: 'clock',
            },
            {
                title: 'Events',
                value: 'events',
                icon: 'calendar',
            },
            {
                title: 'Tags',
                value: 'tags',
                icon: 'tag',
            },
            {
                title: 'Keywords',
                value: 'keywords',
                icon: 'tag',
            },
            {
                title: 'Realms',
                value: 'realms',
                icon: 'bullseye',
            },
            {
                title: 'Photographers',
                value: 'photographers',
                icon: 'camera',
            },
        ];


        return {
            viewModes,
            viewMode: _.find(viewModes, { value: currentRoute }),
        }
    },
    mixins: [BreadcrumbMixin],
    components: {

        PhotoThumbnail,
        PhotoEventTeaser,
        PhotoPhotographerTeaser,
        PhotoKeywordTeaser,
        PhotoRealmTeaser,
        PhotoTagTeaser,
        PhotoRealmTeaser,
    },
    methods: {
        view(item, context) {
            var self = this;
            console.log('ITEM', item, context);

            switch (context) {
                case 'latest':
                    self.$router.push({
                        name: 'photos.latest',
                        query: {
                            photo: item._id,
                        }
                    })

                    break;
                case 'event':
                    self.$router.push({
                        name: 'photos.event',
                        params: {
                            source: item._id,
                        }
                    })

                    break;
                case 'tag':
                    self.$router.push({
                        name: 'photos.tag',
                        params: {
                            source: item._id,
                        }
                    })

                    break;
            }

        },
        // selectViewMode(viewMode) {
        //     switch (viewMode.value) {
        //         default:
        //             this.$router.replace({
        //                 name: `photos.${viewMode.value}`,
        //             })
        //             break;
        //     }
        // }
    },
    computed: {
        routeKey() {
            return this.$route.name.split('.')[0];
        }
    },
    created() {
        console.log('CHECK PEOPLE LANDING', this.routeKey);
    },
    asyncComputed: {

        latest: {
            default: [],
            get() {
                var self = this;
                return new Promise(function(resolve, reject) {

                    self.$fluro.api.get('/content/photo', {
                        params: {
                            sort: '-created',
                            fields: [
                                'title',
                                'managedAuthor',
                                'created',
                                'data.photographer',
                                'data.event',
                                'width',
                                'colors',
                                'height',
                            ],
                            limit: 5,
                        }
                    }).then(function(res) {
                        return resolve(res.data);
                    }, reject);
                })
            }
        },
        events: {
            default: [],
            get() {
                var self = this;
                return new Promise(function(resolve, reject) {

                    self.$fluro.api.get('/photos/event/list', {
                        params: {
                            limit: 4,
                        }
                    }).then(function(res) {
                        return resolve(res.data);
                    }, reject);
                })
            }
        },

        tags: {
            default: [],
            get() {
                var self = this;
                return new Promise(function(resolve, reject) {

                    self.$fluro.api.get('/photos/tag/list', {
                        params: {
                            sort: 'created',
                            limit: 12,
                        }
                    }).then(function(res) {
                        return resolve(res.data);
                    }, reject);
                })
            }
        },

        /**
        
        photographers: {
            default: [],
            get() {
                var self = this;
                return new Promise(function(resolve, reject) {

                    self.$fluro.api.get('/photos/photographer/list', {
                        params: {
                            limit: 4,
                        }
                    }).then(function(res) {
                        return resolve(res.data);
                    }, reject);
                })
            }
        },
        
        
        /**/

    },
}
</script>
